// zamkniecie media dla desktop
//  @extend .button;
.button {
    cursor: pointer;
    background-color: $color;
    color: $text-color;
    font-size: $font-size-m;
    padding-top: 22px;
    padding-bottom: 22px;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
    margin-top: -4px;
    font-weight: normal;
    border-radius: 8px;
    border: 0;
    box-shadow: 0 6px 0 0 #6b2e68;
}
